import { effect, Injectable, signal } from '@angular/core'
import { Router } from '@angular/router'
import {
  signIn,
  signUp,
  signOut,
  fetchAuthSession,
  fetchUserAttributes,
  resetPassword,
  confirmResetPassword,
} from 'aws-amplify/auth'
import { Browser } from '@capacitor/browser'
import {
  CognitoUserPool,
  CognitoUser,
  CognitoUserSession,
  CognitoIdToken,
  CognitoAccessToken,
  CognitoRefreshToken,
} from 'amazon-cognito-identity-js'
import { StorageService } from '../utils/storage.service'
import { Capacitor } from '@capacitor/core'
import { AccountService } from '../api-wrappers/account.service'
import { environment } from 'src/environments/environment'
import { RecaptchaService } from './recaptcha.service'
import { AppstateService } from '../internal/appstate.service'
import { SubscriptionProcessService } from '../utils/subscription-process.service'
import { FcmService } from './fcm.service'
import { captureException } from '@sentry/angular'
import { PushNotifications } from '@capacitor/push-notifications'
import { KYCWrapperService } from '../api-wrappers/kyc.service'
import { ForfaitsWrapperService } from '../api-wrappers/forfaits.service'
import { ErrorHandlerService } from '../utils/error-handler.service'
import { catchError, from, Observable, tap, throwError } from 'rxjs'

export type AuthState =
  | {
      state: 'SignedOut'
    }
  | {
      state: 'SignedIn'
    }

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  public authState = signal<AuthState>({
    state: 'SignedOut',
  })
  private userPool: CognitoUserPool

  constructor(
    private router: Router,
    private storage: StorageService,
    private accountService: AccountService,
    private recaptchaService: RecaptchaService,
    private appStateService: AppstateService,
    private subscriptionProcess: SubscriptionProcessService,
    private fcmService: FcmService,
    private kycWrapperService: KYCWrapperService,
    private forfaitsService: ForfaitsWrapperService,
    private errorHandler: ErrorHandlerService
  ) {
    this.checkAuthStatus()
    this.userPool = new CognitoUserPool({
      UserPoolId: environment.cognito.userPoolId,
      ClientId: environment.cognito.userPoolClientId,
    })

    effect(() => {
      const cyclistState = this.accountService.cyclistState()
      if (cyclistState.state === 'deleted') {
        this.signOut()
      }
      if (
        cyclistState.state === 'error' &&
        this.authState().state === 'SignedIn'
      ) {
        this.signOut()
      }
    })
  }

  public async checkAuthStatus(forceRefresh?: boolean): Promise<void> {
    try {
      const session = await fetchAuthSession({
        forceRefresh:
          forceRefresh !== undefined ? forceRefresh : environment.production,
      })
      if (session.tokens) {
        this.authState.set({ state: 'SignedIn' })
        if (
          this.accountService.cyclistState().state === 'unknown' ||
          this.accountService.cyclistState().state === 'error'
        ) {
          await this.accountService.loadCyclist()

          // Load organization forfaits only if cyclist has memberships
          const cyclistState = this.accountService.cyclistState()
          if (
            'cyclist' in cyclistState &&
            cyclistState.cyclist.registrations?.data?.[0]?.memberships?.data &&
            cyclistState.cyclist.registrations.data[0].memberships.data.length >
              0
          ) {
            try {
              await this.forfaitsService.loadOrganizationForfaits()
            } catch (error) {
              console.warn(
                'Could not load organization forfaits after cyclist load:',
                error
              )
            }
          }
        }
      } else {
        this.authState.set({ state: 'SignedOut' })
      }
    } catch (error) {
      this.authState.set({ state: 'SignedOut' })
    }
  }

  public async signIn(username: string, password: string): Promise<void> {
    try {
      await signIn({
        username,
        password,
        options: {
          authFlowType: 'USER_PASSWORD_AUTH',
        },
      })
      await this.handleSuccessfulAuthentication()
    } catch (error: any) {
      if (error.name === 'UserAlreadyAuthenticatedException') {
        // If user is already authenticated, sign out first and retry
        try {
          await signOut()
          this.authState.set({ state: 'SignedOut' })
          this.resetState()
          // Retry sign in after signout
          await signIn({
            username,
            password,
            options: {
              authFlowType: 'USER_PASSWORD_AUTH',
            },
          })
          await this.handleSuccessfulAuthentication()
          return
        } catch (retryError) {
          this.errorHandler.handleError(retryError)
          throw retryError
        }
      } else if (error.name === 'NotAuthorizedException') {
        try {
          const alternativeSignInResponse = await signIn({
            username,
            password,
          })

          if (alternativeSignInResponse.nextStep.signInStep === 'DONE') {
            await this.handleSuccessfulAuthentication()
            return
          }
        } catch (alternativeError) {
          this.errorHandler.handleError(alternativeError)
          throw alternativeError
        }
      } else if (error.name === 'DeviceMetadataNotFoundException') {
        // Pour Amplify v6, essayons une approche diffrente: nous faisons d'abord une actualisation de la session
        try {
          await fetchAuthSession({ forceRefresh: true })
          // Puis on essaie  nouveau avec l'authentification par dfaut
          const alternativeSignInResponse = await signIn({
            username,
            password,
          })

          if (alternativeSignInResponse.nextStep.signInStep === 'DONE') {
            await this.handleSuccessfulAuthentication()
            return
          }
        } catch (alternativeError) {
          this.errorHandler.handleError(alternativeError)
          throw alternativeError
        }
      }
      this.errorHandler.handleError(error)
      throw error
    }
  }

  private async handleSuccessfulAuthentication(): Promise<void> {
    this.authState.set({ state: 'SignedIn' })
    await this.accountService.loadCyclist()

    // Load organization forfaits only if cyclist has memberships
    const cyclistState = this.accountService.cyclistState()
    if (
      'cyclist' in cyclistState &&
      cyclistState.cyclist.registrations?.data?.[0]?.memberships?.data &&
      cyclistState.cyclist.registrations.data[0].memberships.data.length > 0
    ) {
      try {
        await this.forfaitsService.loadOrganizationForfaits()
      } catch (error) {
        console.warn(
          'Could not load organization forfaits after cyclist load:',
          error
        )
      }
    }

    if (Capacitor.isNativePlatform()) {
      const pushPermission = await PushNotifications.checkPermissions()
      if (pushPermission.receive === 'granted') {
        await this.fcmService.registerPush()
      }
    }

    // Handle external users first - role takes priority
    if (
      'cyclist' in cyclistState &&
      (cyclistState.cyclist.role == 'guest' ||
        cyclistState.cyclist.registrations?.data?.[0]?.role == 'guest')
    ) {
      // External users should verify phone number but skip payment flow
      this.router.navigate(['/verify-phone'], { replaceUrl: true })
      return
    }

    // Handle regular cyclists based on confirmation status
    if (cyclistState.state === 'unconfirmed') {
      this.accountService.hasMissingPaymentMethodOrCgu()
        ? this.router.navigate(['/payment-method'], { replaceUrl: true })
        : this.router.navigate(['/verify-phone'], { replaceUrl: true })
    } else {
      // Check if user needs to complete KYC first
      if (await this.shouldRedirectToKyc()) {
        this.router.navigate(['/kyc'], { replaceUrl: true })
      } else {
        if (this.appStateService.getAppState().state === 'onboarding') {
          this.appStateService.setAppState({
            state: 'new_cyclist',
            dring_state: {
              geolocation_dring: 'not_shown',
            },
            push_notifications: 'not_shown',
          })
        }
        this.router.navigate(['/map'], { replaceUrl: true })
      }
    }
  }

  public async signUp(user: {
    name: string
    email: string
    phone: string
    password: string
  }): Promise<void> {
    try {
      const token = await this.recaptchaService.executeRecaptcha('signup')
      await signUp({
        username: user.phone,
        password: user.password,
        options: {
          userAttributes: {
            name: user.name,
            email: user.email,
            phone_number: user.phone,
            'custom:program': environment.program,
          },
          validationData: { recaptchaToken: token },
          autoSignIn: false,
        },
      })
      await this.signIn(user.phone, user.password)
    } catch (error) {
      this.errorHandler.handleError(error)
    }
  }

  public async signOut(bypassRedirection = false): Promise<void> {
    // Unregister push notifications before signing out
    if (Capacitor.isNativePlatform()) {
      await PushNotifications.checkPermissions()
        .then(async (permission) => {
          if (permission.receive === 'granted') {
            await this.fcmService.unRegisterPush()
          }
        })
        .then(async () => {
          await signOut().then(() => {
            this.resetState()
            if (!bypassRedirection) {
              this.router.navigate(['/map'])
            }
          })
        })
    } else {
      await signOut().then(() => {
        this.resetState()
        if (!bypassRedirection) {
          this.router.navigate(['/map'])
        }
      })
    }
  }
  resetState() {
    this.authState.set({ state: 'SignedOut' })
    this.accountService.cyclistState.set({
      state: 'unknown',
      blocked_reasons: ['Unknown account state'],
    })
    this.storage.remove('me')
    this.subscriptionProcess.clearProcess()
  }
  public async signInWithSocialProvider(
    provider: 'Google' | 'Apple'
  ): Promise<void> {
    const liveMode = environment.production
    const program = await this.storage.get('program')
    if (!program) {
      this.errorHandler.handleError({ error: { title: 'program_not_found' } })
    }
    if (Capacitor.isNativePlatform()) {
      await Browser.open({
        url: `https://auth.ecovelo.mobi/auth-initiate?provider=${provider}&liveMode=${liveMode}&appBundle=${
          program.config.app_bundle ? program.config.app_bundle : program.id
        }`,
        presentationStyle: 'popover',
      })
    } else {
      if (!program?.config?.webapp) {
        this.errorHandler.handleError({ error: { title: 'program_not_found' } })
        return
      }
      window.location.href = `http://auth.ecovelo.mobi/auth-initiate?provider=${provider}&liveMode=${liveMode}&webAppUrl=${program.config.webapp}`
    }
  }

  /**
   * Redirects the user to the Fleetiz auth portal with a valid access token.
   * On native, opens in an in-app browser; on web, performs a full page redirect.
   */
  public async redirectToLld(): Promise<void> {
    try {
      const session = await fetchAuthSession({ forceRefresh: false })
      const accessToken = session?.tokens?.accessToken?.toString()
      if (!accessToken) {
        this.errorHandler.handleError({
          error: { title: 'access_token_not_found' },
        })
        return
      }

      const program = await this.storage.get('program')
      if (!program) {
        this.errorHandler.handleError({ error: { title: 'program_not_found' } })
        return
      }

      // Add extOrigin parameter based on platform with robust validation
      let extOrigin = ''
      if (Capacitor.isNativePlatform()) {
        if (!program.config.app_bundle) {
          this.errorHandler.handleError({
            error: { title: 'app_bundle_not_found' },
          })
          return
        }
        extOrigin = program.config.app_bundle
      } else {
        if (!program.config.webapp) {
          this.errorHandler.handleError({
            error: { title: 'webapp_not_found' },
          })
          return
        }
        extOrigin = program.config.webapp
      }

      // Include extOrigin as query parameter within the target URL
      const targetUrl = `${
        program.config.lld_url
      }${accessToken}&extOrigin=${encodeURIComponent(extOrigin)}`
      const url = `https://auth.ecovelo.mobi/auth-redirect?url=${encodeURIComponent(
        targetUrl
      )}`

      if (Capacitor.isNativePlatform()) {
        await Browser.open({ url, presentationStyle: 'fullscreen' })
      } else {
        window.location.href = url
      }
    } catch (error) {
      this.errorHandler.handleError(error)
      throw error
    }
  }

  public async createSessionFromTokens(sessionData: {
    idToken: string
    accessToken: string
    refreshToken: string
  }): Promise<CognitoUserSession> {
    const idToken = new CognitoIdToken({ IdToken: sessionData.idToken })
    const accessToken = new CognitoAccessToken({
      AccessToken: sessionData.accessToken,
    })
    const refreshToken = new CognitoRefreshToken({
      RefreshToken: sessionData.refreshToken,
    })

    const session = new CognitoUserSession({
      IdToken: idToken,
      AccessToken: accessToken,
      RefreshToken: refreshToken,
    })
    const user = new CognitoUser({
      Username: session.getAccessToken().payload['username'],
      Pool: this.userPool,
    })
    user.setSignInUserSession(session)
    return session
  }

  public async getUserPhoneNumber(): Promise<string> {
    const user = await fetchUserAttributes()
    if (user.phone_number) {
      return user.phone_number
    } else {
      captureException(new Error('No phone number found for user'))
      return ''
    }
  }

  /**
   * Actualise explicitement la session d'authentification.
   * Cette mthode est utile avant d'effectuer des oprations sensibles comme
   * les tlchargements vers S3, surtout aprs que l'application a t en arrire-plan.
   * @returns Rsultat de la session actualise
   */
  public async refreshSession() {
    try {
      return await fetchAuthSession({ forceRefresh: true })
    } catch (error) {
      console.warn('Failed to refresh authentication session:', error)
      this.errorHandler.handleError(error)
      throw error
    }
  }

  private async shouldRedirectToKyc(): Promise<boolean> {
    // Only redirect if user is signed in and has KYC documents enabled
    if (
      this.authState().state !== 'SignedIn' ||
      this.kycWrapperService.enabledDocumentTypes().length === 0
    ) {
      return false
    }

    // Ensure cyclist data is loaded
    let cyclistState = this.accountService.cyclistState()

    // If cyclist data is not loaded yet, wait for it
    if (cyclistState.state === 'unknown' || cyclistState.state === 'error') {
      await this.accountService.loadCyclist()
      cyclistState = this.accountService.cyclistState()
    }

    if (cyclistState.state === 'unknown' || cyclistState.state === 'error') {
      return false
    }

    const kycState = this.kycWrapperService.kycCompletionState()
    return kycState === 'missing' || kycState === 'rejected'
  }
}
