import { Injectable } from '@angular/core'
import {
  ForfaitsService,
  ListForfait200ResponseAllOfDataInner,
} from 'src/api-cyclist'
import { environment } from 'src/environments/environment'
import { StorageService } from '../utils/storage.service'
import { ErrorHandlerService } from '../utils/error-handler.service'
import { AccountService } from './account.service'

@Injectable({
  providedIn: 'root',
})
export class ForfaitsWrapperService {
  constructor(
    private forfaitsService: ForfaitsService,
    private storage: StorageService,
    private errorHandler: ErrorHandlerService,
    private accountService: AccountService
  ) {}

  async loadForfaits() {
    try {
      const [defaultForfaitResponse, forfaitsResponse] = await Promise.all([
        this.forfaitsService
          .listForfait({
            program: environment.program,
            expand: ['fiche'],
            type: 'default',
            limit: 1,
          })
          .toPromise(),
        this.forfaitsService
          .listForfait({
            program: environment.program,
            expand: ['fiche'],
            type: 'classic',
            limit: 100,
          })
          .toPromise(),
      ])

      // Vrifier qu'il y a un forfait par dfaut
      const defaultForfait = defaultForfaitResponse?.data?.[0]
      if (!defaultForfait) {
        throw new Error('Aucun forfait par dfaut trouv')
      }

      // Sauvegarder le forfait par dfaut mme si la liste des forfaits est vide
      await this.storage.set('defaultForfait', defaultForfait)

      // Sauvegarder la liste des forfaits si elle existe
      if (forfaitsResponse?.data) {
        await this.storage.set('forfaits', forfaitsResponse.data)
      } else {
        // Si pas de forfaits classiques, sauvegarder une liste vide
        await this.storage.set('forfaits', [])
      }
    } catch (error) {
      this.errorHandler.handleError(error)
      throw error
    }
  }

  async getForfaits(): Promise<ListForfait200ResponseAllOfDataInner[]> {
    return await this.storage.get('forfaits')
  }

  async getDefaultForfait(): Promise<
    ListForfait200ResponseAllOfDataInner | undefined
  > {
    return await this.storage.get('defaultForfait')
  }

  /**
   * Charge uniquement les forfaits d'organisation et les ajoute aux forfaits existants
   * Utile pour ajouter les forfaits d'organisation aprs le chargement initial
   */
  async loadOrganizationForfaits(): Promise<void> {
    try {
      const cyclistState = this.accountService.cyclistState()

      // Vrifier que le cyclist est charg et disponible
      if (
        cyclistState.state === 'unknown' ||
        cyclistState.state === 'error' ||
        !('cyclist' in cyclistState)
      ) {
        console.warn('Cyclist not available for loading organization forfaits')
        return
      }

      const cyclist = cyclistState.cyclist
      const registration = cyclist.registrations?.data?.[0]

      if (
        !registration?.memberships?.data ||
        registration.memberships.data.length === 0
      ) {
        // Pas de memberships, rien  faire
        return
      }

      // Rcuprer les forfaits existants
      let allForfaits: ListForfait200ResponseAllOfDataInner[] =
        (await this.storage.get('forfaits')) || []

      // Pour chaque membership, rcuprer les forfaits de l'organisation
      const membershipForfaitsPromises = registration.memberships.data.map(
        (membership) => {
          // membership peut tre un string ou un objet avec organization
          const organizationId =
            typeof membership === 'string'
              ? membership
              : membership.organization
          return this.forfaitsService
            .listForfait({
              program: environment.program,
              expand: ['fiche'],
              organization: organizationId,
              limit: 100,
            })
            .toPromise()
        }
      )

      const membershipForfaitsResponses = await Promise.all(
        membershipForfaitsPromises
      )

      // Ajouter les forfaits d'organisation  la liste existante
      for (const response of membershipForfaitsResponses) {
        if (response?.data) {
          // Filtrer les doublons en vrifiant les IDs
          const newForfaits = response.data.filter(
            (newForfait) =>
              !allForfaits.some(
                (existingForfait) => existingForfait.id === newForfait.id
              )
          )
          allForfaits = [...allForfaits, ...newForfaits]
        }
      }

      // Sauvegarder la liste mise  jour
      await this.storage.set('forfaits', allForfaits)
    } catch (error) {
      this.errorHandler.handleError(error)
      console.error('Error loading organization forfaits:', error)
    }
  }

  /**
   * Vrifie s'il y a des forfaits disponibles dans le storage
   */
  async hasForfaits(): Promise<boolean> {
    try {
      const forfaits = await this.getForfaits()
      return forfaits && forfaits.length > 0
    } catch (error) {
      return false
    }
  }
}
